        v-for="item in suggestionsArr"
        :key="item.keyword"
        @click="
          value = item.keyword;
          $router.push({ path: `/so/${value}` });
          storageHistory(item.keyword);
          $emit('close-page');
        "
      >
        <div>
          {{ item.keyword }}
        </div>
      </li>
