</template>

<script>
import { mapMutations } from "vuex";

export default {
  props: {
    suggestionsArr: Array,
    suggsetTab: Array,
  },
  methods: {
    ...mapMutations(["storageHistory"]),
    chuli: function (res) {
      if (res.startsWith("http")) {
        return res;
      } else if (!res.startsWith("http")) {
        return `https://imagev2.xmcdn.com/${res}!op_type=3&columns=144&rows=144&magick=webp`;
      } else {
        return "1.png";
      }
    },
    closeBody: function () {
      document.querySelector("body").style.overflow = "initial";
    },
  },
};
</script>

<style lang="less" scoped>
.itemFirst {
  padding: 0 !important;
  .itemBox {
    padding: 0 !important;
