    align-items: center;
    .img {
      width: 40px;
      height: 40px;
      margin: 10rem;
      margin-left: 0;
      flex-shrink: 0;
      img {
        width: 100%;
        height: 100%;
        border-radius: 3px;
      }
    }
    .center {
      width: calc(100% - 60rem);
      .title {
        font-size: 14rem;
      }
      .content {
        display: flex;
        align-items: center;
        color: #a3a3ac;
        margin-top: 5rem;
        .category {
          border-radius: 5rem;
          border: 1rem solid #a3a3ac;
          padding: 0 5rem;
          margin-right: 10rem;
        }
        .play {
          display: flex;
          align-items: center;
          svg {
            margin-right: 5rem;
            width: 15rem;
            height: 15rem;
          }
          margin-right: 10rem;
        }
        .tracks {
          display: flex;
          align-items: center;
          svg {
            margin-right: 5rem;
            width: 15rem;
            height: 15rem;
          }
        }
      }
    }
    > .play {
      border-radius: 50%;
      background-color: #a3a3ac;
      font-size: 0;
      svg {
        width: 25rem;
        transform: scale(1.1);
        height: 25rem;
      }
    }
  }
}
.suggestList {
  padding: 10rem;
  .error {
    padding: 10rem;
    position: absolute;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .item {
    > div {
      padding: 11rem 0;
      border-bottom: calc(1rem * 0.5) solid rgba(0, 0, 0, 0.103);
      font-size: 14rem;
