<template>
  <div id="app">
    <audio
      ref="audio"
      :src="
        chuli(soundSrc ?? '') ? soundSrc : soundSrc.trackDetailInfo?.trackInfo.playPath
      "
      @pause="changeMusic(false)"
      @play="changeMusic(true)"
      @durationchange="duration($event.target.duration)"
      @timeupdate="updateTime($event.target.currentTime)"
      controls
    ></audio>
    <div class="playMusicBar">
      <PlayMusicBar
        @play-Music="$refs.audio.play()"
        @stop-Music="$refs.audio.pause()"
        :tabhide="tabhide"
      />
    </div>
    <router-view :key="routerState" />
  </div>
</template>
<script>
import PlayMusicBar from "@/components/playMusicModel/playMusicBar.vue";
import { mapState, mapMutations } from "vuex";

export default {
  data() {
    return {
      oldScrollTop: 0,
      tabhide: true,
    };
  },
  // name: "HomeView",
  components: {
    PlayMusicBar,
  },
  computed: {
    ...mapState([
      "showPage",
      "soundSrc",
      "play",
      "timeupdate",
      "routerState",
      "audioplay",
    ]),
  },
  mounted() {
    window.addEventListener("scroll", this.scrolling);
    this.playMusic(this.$refs.audio);
  },
  methods: {
    ...mapMutations([
      "changeMusic",
      "playMusic",
      "changeMusic",
      "duration",
      "updateTime",
    ]),

    scrolling() {
      try {
        let scrollTop =
          window.pageYOffset ||
          document.documentElement.scrollTop ||
          document.body.scrollTop;

        let scrollStep = scrollTop - this.oldScrollTop;
        this.oldScrollTop = scrollTop;
        if (scrollStep < 0) {
          this.tabhide = true;
        } else {
          this.tabhide = false;
        }
      } catch (error) {
        error;
      }
    },
    chuli: function (res) {
      if (res !== "") {
        try {
          let ok = res.startsWith("http");
          // console.log(res.startsWith("http"));
          return ok;
        } catch (error) {
          // console.log("no");
          error;
          return false;
        }
      }
    },
  },
  watch: {
    timeupdate: function () {
      if (!this.$refs.audio.paused) {
        this.changeMusic(true);
      } else {
        this.changeMusic(false);
      }
    },
  },
};
</script>

