</template>
<script>
import { Category, boardList } from "../apis";
import { mapState, mapMutations } from "vuex";
// import { Toast } from "vant";
export default {
  // props: ["historyRecord"],
  data() {
    return {
      categoryNav: null,
      categoryContent: null,
      //加载热搜中的loading
      loading: true,
      //注入搜索时的数据
      value: null,
    };
  },
  computed: {
    ...mapState(["historyRecord"]),
  },
  created() {
    this.loading = true;
    //nav列表
    let first = Category();
    //nav里面对应的东西
    let sed = boardList(-1);
    Promise.all([first, sed]).then((res) => {
      this.categoryNav = res[0].data;
      this.categoryContent = res[1].data;
      this.loading = false;
    });
  },
  methods: {
    ...mapMutations(["storageHistory", "clearHistory"]),
    changeNav: function (id) {
      this.loading = true;
      boardList(id).then((res) => {
        this.categoryContent = res.data;
        this.loading = false;
      });
    },
    onClick(title) {
      let id = this.categoryNav.category[title].categoryId;
      this.changeNav(id);
    },
    clearHis() {
      this.clearHistory();
      localStorage.removeItem("historyRecord");
    },
  },
};
</script>
