<template>
  <!-- 新品榜 -->
  <div class="NewRanking">
    <ul class="NewRanking-left">
      <li
        v-for="(item, i) in List[1]?.categoryInfos"
        :key="item?.rankClusterId"
        :item="item"
        :class="{ active: hide === i }"
        @click="getItem(item?.categoryCode, i)"
      >
        {{ item?.title }}
      </li>
    </ul>
    <ul class="NewRanking-right">
      <li v-for="(item, i) in Items" :key="item?.rankClusterId" :item="item">
        <div class="rightLi-left">
          <div
            class="Li-leftC"
            :class="{ o1: i + 1 === 1, o2: i + 1 === 2, o3: i + 1 === 3 }"
          >
            {{ i + 1 }}
          </div>
        </div>
        <div class="rightLi-right">
          <div class="rr-left">
            <van-image
              width="70rem"
              height="70rem"
              lazy-load
              :src="chuli(item?.albumInfo?.cover)"
            />
          </div>
          <div class="rr-right">
            <h1>
              {{ item?.albumInfo?.title }}
            </h1>
            <div class="rr-bottom">
              <img src="../../assets/Rank/Rank4.png" alt="" />
              <span>{{ youPlya(item?.statCountInfo?.playCount) }}</span>
              <img src="../../assets/Rank/Rank5.png" alt="" />
              <span>
                {{ item?.statCountInfo?.trackCount }}
              </span>
            </div>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
// 新品榜
export default {
  props: {
    List: Array,
  },
  data() {
    return {
      hide: 0,
      Items: [],
    };
  },
  computed: {},
  created() {
    this.getCode("youshengshu");
  },
  methods: {
    chuli: function (n) {
      if (n.startsWith("http")) {
        return n;
      } else if (!n.startsWith("http")) {
        return `https://imagev2.xmcdn.com/${n}!op_type=3&columns=144&rows=144&magick=webp`;
      } else {
        return "1.png";
      }
    },
    getItem(n, i) {
      this.getCode(n);
      this.hide = i;
    },
    getCode(n) {
      this.axios
        .get("https://apis.netstart.cn/ximalaya/rank/queryRank", {
          params: {
            clusterCode: this.List[1]?.clusterCode,
            categoryCode: n,
          },
        })
        .then((res) => {
          this.Items = res.data.data.rankModuleInfoList;
        });
    },
    youPlya: function (n) {
      if (n / 100000000 >= 1) {
        n = Math.floor(n / 1000000) / 100 + "亿";
        return n;
      } else if (n / 10000 >= 1) {
        n = Math.floor(n / 100) / 100 + "万";
        return n;
      }
      return n;
    },
  },
};
</script>

<style lang="less" scoped>
.NewRanking {
  margin-top: 55rem;
  width: 100%;
  position: relative;
  .NewRanking-right {
    position: absolute;
    top: 0;
    left: 73rem;
    height: 567rem;
    overflow-y: auto;
    overflow-x: hidden;
    li {
      width: 302rem;
      display: flex;
      .rightLi-right {
        width: 265rem;
        padding: 15rem 10rem 15rem 0;
        display: flex;
        border-bottom: 1px solid #d7d7d7;
        .rr-right {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          h1 {
            width: 160rem;
            font-size: 16rem;
            line-height: 16rem;
            color: #40404c;
            font-weight: 500;
            display: -webkit-box;
            -o-text-overflow: ellipsis;
            text-overflow: ellipsis;
            -webkit-box-orient: vertical;
          }
          .rr-bottom {
            display: flex;
            font-size: 11rem;
            line-height: 1.45;
            white-space: nowrap;
            color: #999;
            margin: 15rem 0 10rem;
            align-items: center;
            span {
              margin-right: 10rem;
            }
            img {
              margin-right: 5rem;
            }
          }
        }

        .rr-left {
          width: 85rem;
          height: 70rem;
          padding-right: 15rem;
          img {
            width: 70rem;
            height: 70rem;
          }
        }
      }
      .rightLi-left {
        width: 34rem;
        height: 100%;
        .Li-leftC {
          width: 100%;
          margin: 18rem 0;
          height: 19rem;
          line-height: 19rem;
          color: #40404c;
          text-align: center;
          font-size: 16rem;
          font-weight: 600;
          position: relative;
          &.o1 {
            &::after {
              content: "";
              position: absolute;
              display: block;
              width: 18rem;
              height: 18rem;
              top: 0;
              left: 9rem;
              background-image: url(../../assets/Rank/Rank.png);
              background-size: 18rem 18rem;
              background-repeat: no-repeat;
            }
          }
          &.o2 {
            &::after {
              content: "";
              position: absolute;
              display: block;
              width: 18rem;
              height: 18rem;
              top: 0;
              left: 9rem;
              background-image: url(../../assets/Rank/Rank2.png);
              background-size: 18rem 18rem;
              background-repeat: no-repeat;
            }
          }
          &.o3 {
            &::after {
              content: "";
              position: absolute;
              display: block;
              width: 18rem;
              height: 18rem;
              top: 0;
              left: 9rem;
              background-image: url(../../assets/Rank/Rank3.png);
              background-size: 18rem 18rem;
              background-repeat: no-repeat;
            }
          }
        }
      }
    }
  }
  ::-webkit-scrollbar {
    display: none;
  }
  .NewRanking-left {
    position: absolute;
    top: 0;
    left: 0;
    height: 570rem;
    white-space: nowrap;
    overflow: auto;

    li {
      width: 73rem;
      height: 50rem;
      display: block;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      text-align: center;
      font-size: 14rem;
      padding: 15rem 0;
      line-height: 20rem;
      color: #72727b;
      background-color: #f3f4f5;
      position: relative;
      &.active {
        background: #fff;
        &::before {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          width: 5rem;
          height: 100%;
          background-color: red;
        }
      }
      &::after {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        border-bottom: 1px solid rgb(215 215 215 / 30%);
      }
    }
  }
}
</style>
