    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    z-index: 10;
    font-size: 15rem;
    img {
      margin-top: 100rem;
    }
  }
}
.headerBox {
  width: 100%;
  height: 50rem;
  background-color: #fff;
  position: fixed;
  top: 0;
