  margin: 10rem 0;
  padding: 0 10rem;
  height: 30rem;
  // .headerBox {
  //   height: 50rem;
  //   background: #fff;
  // }
  .logo {
    flex-shrink: 0;
    width: 60rem;
    height: 100%;
