  }
  ::placeholder {
    color: #989898;
  }
  .search {
    width: 100%;
    position: relative;
    background-color: #eff1f4;
    border-radius: 50rem;
    margin: 0 10rem;
    svg {
      position: absolute;
      top: 50%;
      left: 8rem;
      transform: translateY(-50%);
      width: 15rem;
