<template>
  <div class="page">
    <header>
      <div class="search">
        <svg
          t="1664715880181"
          class="icon left"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="5518"
          width="48"
          height="48"
        >
          <path
            d="M851.710029 803.043632c67.147306-78.301344 107.720374-180.059935 107.720373-291.298435 0-247.27171-200.453405-447.724091-447.724091-447.724091S63.981197 264.473487 63.981197 511.745197s200.453405 447.724091 447.724091 447.724091c111.2385 0 212.997092-40.573067 291.298435-107.720374l97.632622 97.632621c13.449313 13.449313 35.255968 13.449313 48.706305 0 13.449313-13.449313 13.449313-35.255968 0-48.706305l-97.632621-97.631598z m-67.805292-19.10001l-22.499434 19.294439c-34.062793 29.209252-72.504314 52.006468-114.25623 67.759243-43.206034 16.301268-88.775906 24.566512-135.441739 24.566512-102.521978 0-198.907189-39.924291-271.40127-112.417349s-112.417349-168.879292-112.417349-271.40127 39.924291-198.907189 112.417349-271.40127S409.184333 127.926578 511.706311 127.926578c102.521978 0 198.907189 39.924291 271.40127 112.417349s112.417349 168.879292 112.417349 271.40127c0 46.666856-8.265244 92.236727-24.566512 135.441738-15.752776 41.75294-38.549991 80.193437-67.759243 114.256231l-19.294438 22.500456z"
            fill="#bfbfbf"
            p-id="5519"
          ></path>
        </svg>
        <!--  -->
        <input
          type="text"
          v-model="value"
          @keyup.enter="
            storageHistory(value);
            value && value !== $route.params.id
              ? $router.push({ path: `/so/${value}` })
              : false;
          "
          @close-page="$emit('Close-Page')"
          ref="searchInput"
        />
        <svg
          @click="value = ''"
          t="1664716866913"
          class="icon right"
          viewBox="0 0 1025 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="6735"
          width="48"
          height="48"
          v-show="value"
        >
          <path
            d="M513.024 1024C230.254592 1024 1.024 794.769408 1.024 512S230.254592 0 513.024 0s512 229.230592 512 512-229.230592 512-512 512z m0-589.579264l-168.0896-168.0896-77.579264 77.579264L435.444736 512l-168.0896 168.0896 77.579264 77.579264L513.024 589.579264l168.0896 168.0896 77.579264-77.579264L590.603264 512l168.0896-168.0896-77.579264-77.579264L513.024 434.420736z"
            p-id="6736"
            fill="#989898"
          ></path>
        </svg>
      </div>
      <div
        class="close"
        @click="
          $emit('Close-Page');
          value = '';
        "
      >
        取消
      </div>
    </header>
    <!-- 搜索建议 -->
    <SuggestionsPage
      @chang-Record="storageHistory"
      @close-page="$emit('Close-Page')"
      :suggestionsArr="suggestionsArr"
      v-show="suggestionsArr"
      :suggsetTab="suggsetTab"
    />
    <!-- 热搜 -->
    <keep-alive>
      <SecondarySearch
        v-show="!suggestionsArr"
        @close-page="$emit('Close-Page')"
        @chang-Record="storageHistory"
      />
    </keep-alive>
  </div>
</template>

<script>
import SecondarySearch from "@/components/SecondarySearch.vue";
import SuggestionsPage from "@/components/SuggestionsPage.vue";
var _ = require("lodash");
import { mapMutations } from "vuex";

export default {
  props: {
    showSearchPage: Boolean,
  },
  components: {
    SecondarySearch,
    SuggestionsPage,
  },
  data() {
    return {
      // 搜索结果跳路由
      //绑定input
      value: null,
      //历史记录
      historyArr: [],
      // 热搜词
      hotSearchArr: null,
      //搜索建议
      suggestionsArr: null,
      //搜索建议跳转歌单
      suggsetTab: null,
      //历史记录
      // historyRecord: JSON.parse(window.localStorage.getItem("historyRecord")) ?? [],
    };
  },

  created() {
    this.searchContDebounce = _.debounce(this.searchCont, 800);
  },
  methods: {
    ...mapMutations([
      "storageHistory", //also supports payload `this.nameOfMutation(amount)`
    ]),
    searchCont: function (value) {
      this.axios
        .get("https://m.ximalaya.com/revision/suggest", {
          params: {
            kw: value,
          },
        })
        .then((res) => {
          this.suggsetTab = res.data.data.result.albumResultList;
          this.suggestionsArr = this.value ? res.data.data.result.queryResultList : null;
        });
    },
  },
  watch: {
    showSearchPage(status) {
      if (status) {
        this.$refs.searchInput.focus();
      } else {
        this.$refs.searchInput.blur();
      }
    },
    //监听value的变化
    value: function (value) {
      if (value) {
        this.searchContDebounce(value);
      } else {
        this.suggestionsArr = null;
      }
    },
  },
};
</script>

<style lang="less" scoped>
::-webkit-scrollbar {
  width: 5px;
  display: none;
}
/*调整滚动条的个体颜色和圆角*/
::-webkit-scrollbar-thumb {
  border-radius: 0px;
  display: none;
  background-color: rgba(36, 226, 255, 0);
}
/* 调整滚动条框的背景颜色 和其圆角形状*/
::-webkit-scrollbar-track {
  border-radius: 0px;
  display: none;
  background-color: #ffffff00;
}
.page {
  overflow: auto;
  width: 100%;
  height: 100vh;
  background-color: #fff;
  font-size: 15rem;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 99;
  header {
    display: flex;
    align-items: center;
    padding: 10rem 10rem 0 10rem;
    .search {
      width: 100%;
      position: relative;
      background-color: #eff1f4;
      border-radius: 50rem;
      margin: 0 5rem;
      svg.left {
        position: absolute;
        top: 50%;
        left: 8rem;
        transform: translateY(-50%);
        width: 15rem;
        height: 15rem;
      }
      svg.right {
        position: absolute;
        top: 50%;
        right: 8rem;
        transform: translateY(-50%);
        width: 15rem;
        height: 15rem;
      }
      input {
        width: 100%;
        border: 0;
        outline: 0;
        padding: 8rem 30rem;
        background-color: transparent;
      }
    }
    .close {
      padding: 0 5rem;
      font-size: 16rem;
      flex-shrink: 0;
    }
  }
}
</style>
