<template>
  <div>
    <div class="top">
      <div class="header">
        <div
          class="logo"
          @click="$route.path !== '/tuijian' ? $router.push({ path: '/' }) : ''"
        ></div>
        <div class="search">
          <svg
            t="1664715880181"
            class="icon"
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            p-id="5518"
            width="48"
            height="48"
          >
            <path
              d="M851.710029 803.043632c67.147306-78.301344 107.720374-180.059935 107.720373-291.298435 0-247.27171-200.453405-447.724091-447.724091-447.724091S63.981197 264.473487 63.981197 511.745197s200.453405 447.724091 447.724091 447.724091c111.2385 0 212.997092-40.573067 291.298435-107.720374l97.632622 97.632621c13.449313 13.449313 35.255968 13.449313 48.706305 0 13.449313-13.449313 13.449313-35.255968 0-48.706305l-97.632621-97.631598z m-67.805292-19.10001l-22.499434 19.294439c-34.062793 29.209252-72.504314 52.006468-114.25623 67.759243-43.206034 16.301268-88.775906 24.566512-135.441739 24.566512-102.521978 0-198.907189-39.924291-271.40127-112.417349s-112.417349-168.879292-112.417349-271.40127 39.924291-198.907189 112.417349-271.40127S409.184333 127.926578 511.706311 127.926578c102.521978 0 198.907189 39.924291 271.40127 112.417349s112.417349 168.879292 112.417349 271.40127c0 46.666856-8.265244 92.236727-24.566512 135.441738-15.752776 41.75294-38.549991 80.193437-67.759243 114.256231l-19.294438 22.500456z"
              fill="#bfbfbf"
              p-id="5519"
            ></path>
          </svg>
          <input type="text" placeholder="搜索" @focus="showSearch()" />
        </div>
        <div class="openBtn">打开app</div>
      </div>
      <nav class="list" ref="list" v-if="tabList?.length">
        <li
          class="list-smallbox"
          :class="{ active: $route.path == `/${item.moduleKey}` }"
          v-for="item in tabList"
          :key="item.moduleKey"
          @click="changeName(item.moduleKey)"
        >
          <router-link :to="`/${item.moduleKey}?name=${item.moduleKey}`">
            {{ item.moduleName }}
          </router-link>
        </li>
        <li
          class="list-smallbox"
          :class="{ active: $route.path == `/category` }"
          @click="changeName(item.moduleKey)"
        >
          <router-link :to="`/category`"> 更多 </router-link>
        </li>
      </nav>
    </div>
    <transition name="showPage">
      <searchPage
        class="searchPotion"
        v-show="showSearchPage"
        @Close-Page="closeSearch()"
        :showSearchPage="showSearchPage"
      />
    </transition>
    <keep-alive>
      <router-view @change-footer="changeState" style="margin-top: 85.08rem" />
    </keep-alive>

    <footer v-show="footerState">
      <img src="../assets/2.png" alt="" />
      <div class="img">
        <img src="../assets/3.png" alt="" />
        <span>&copy; 2014-2022 喜马拉雅 版权所有</span>
      </div>
    </footer>
  </div>
</template>

<script>
import searchPage from "@/components/searchPage.vue";

// import {} from "module";
import { mapMutations, mapState } from "vuex";

export default {
  components: {
    searchPage,
  },
  computed: {
    ...mapState(["footerState", "routerState"]),
  },
  methods: {
    ...mapMutations(["changeName", "changeState"]),

    scrolling() {
      try {
        let scrollTop =
          window.pageYOffset ||
          document.documentElement.scrollTop ||
          document.body.scrollTop;

        let scrollStep = scrollTop - this.oldScrollTop;
        this.oldScrollTop = scrollTop;

        if (scrollStep < 0) {
          this.$refs.list.style.height = "35rem";
        } else {
          this.$refs.list.style.height = "0rem";
        }
        if (scrollTop == 0) {
          // console.log("到头了");
          this.$refs.list.style.height = "35rem";
        }
      } catch (error) {
        error;
      }
    },
    showSearch() {
      this.showSearchPage = true;
      document.querySelector("body").style.overflow = "hidden";
    },
    closeSearch() {
      this.showSearchPage = false;
      document.querySelector("body").style.overflow = "initial";
    },
  },
  created() {
    this.axios
      .get("https://apis.netstart.cn/ximalaya/index/queryIndexTabModule")
      .then((res) => {
        this.tabList = res.data.data;
      });
  },
  mounted() {
    window.addEventListener("scroll", this.scrolling);
  },
  data: function () {
    return {
      tabList: null,
      oldScrollTop: 0,
      showSearchPage: false,
    };
  },
};
</script>

<style lang="less" scoped>
.showPage-enter-active,
.showPage-leave-active {
  transition: transform 0.5s;
}
.showPage-enter,
.showPage-leave-to {
  transform: translateY(100%);
}
::-webkit-scrollbar {
  width: 5px;
  display: none;
}
/*调整滚动条的个体颜色和圆角*/
::-webkit-scrollbar-thumb {
  border-radius: 0px;
  display: none;
  background-color: rgba(36, 226, 255, 0);
}
/* 调整滚动条框的背景颜色 和其圆角形状*/
::-webkit-scrollbar-track {
  border-radius: 0px;
  display: none;
  background-color: #ffffff00;
}
.top {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
  z-index: 12;
  .header {
    display: flex;
    align-items: center;
    margin: 10rem;
    height: 30rem;
    .logo {
      flex-shrink: 0;
      width: 60rem;
      height: 100%;
      background-image: url(../assets/3.png);
      background-position: -25rem 2rem;
      background-size: 250rem;
      // margin-right: 10rem;
    }
    ::placeholder {
      color: #989898;
    }
    .search {
      width: 100%;
      position: relative;
      background-color: #eff1f4;
      border-radius: 50rem;
      margin: 0 10rem;
      svg {
        position: absolute;
        top: 50%;
        left: 8rem;
        transform: translateY(-50%);
        width: 15rem;
        height: 15rem;
      }
      input {
        border: 0;
        outline: 0;
        padding: 8rem 0 8rem 30rem;
        background-color: transparent;
      }
    }
    .openBtn {
      flex-shrink: 0;
      line-height: 10rem;
      padding: 10rem;
      height: 100%;
      border: 1rem solid #f66847;
      color: #f66847;
      border-radius: 50rem;
    }
  }
  .list {
    transition: all 0.3s;
    padding: 0 5rem;
    height: 35rem;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: space-between;
    overflow-x: auto;
    overflow-y: hidden;
    list-style: none;
    box-shadow: 0 1rem 1rem 0 rgba(0, 0, 0, 0.164);
    .list-smallbox {
      flex-shrink: 0;
      text-align: center;
      margin-right: 15rem;
      transition: all 0.3s;
      margin-left: 5rem;
      &.active {
        a {
          // border-bottom: 2rem solid #12c;
          position: relative;
          font-size: 18rem;

          &::after {
            position: absolute;
            bottom: -1rem;
            left: 50%;
            transform: translateX(-50%);
            content: "";
            width: calc(100% / 2);
            height: 2rem;
            border-radius: 50rem;
            background-color: #f66847;
          }
        }
      }
      a {
        display: block;
        font-size: 13rem;
        text-decoration: none;
        color: #40404c;
        padding-bottom: 10rem;
        transition: all 0.3s;
      }
    }
  }
}
footer {
  position: relative;
  > img {
    display: block;
  }
  .img {
    position: absolute;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    top: 25%;
    padding: 35rem;
    span {
      font-size: 12rem;
      margin-top: 20rem;
      color: #73737b;
    }
  }
}
</style>
