<script>
// import TuiJianItem from "../tuijian/TuiJianItem.vue";
export default {
  // components: { TuiJianItem },
  props: {
    info: Array,
  },
  methods: {
    chuli: function (res) {
      if (res?.startsWith("http")) {
        return res;
      } else if (res?.startsWith("http") == false) {
        return `https://imagev2.xmcdn.com${
          res[0] === "/" ? res : "/" + res
        }!op_type=5&device_type=ios&name=web_meduim&upload_type=cover&magick=webp`;
      } else {
        return "1.png";
      }
    },
  },
};
</script>

