<template>
  <div class="RankingList">
    <div class="header">
      <div
        class="logo"
        @click="$router.path != '/tuijian' ? $router.push({ path: '/' }) : ''"
      ></div>
      <div class="search">
        <svg
          t="1664715880181"
          class="icon"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="5518"
          width="48"
          height="48"
        >
          <path
            d="M851.710029 803.043632c67.147306-78.301344 107.720374-180.059935 107.720373-291.298435 0-247.27171-200.453405-447.724091-447.724091-447.724091S63.981197 264.473487 63.981197 511.745197s200.453405 447.724091 447.724091 447.724091c111.2385 0 212.997092-40.573067 291.298435-107.720374l97.632622 97.632621c13.449313 13.449313 35.255968 13.449313 48.706305 0 13.449313-13.449313 13.449313-35.255968 0-48.706305l-97.632621-97.631598z m-67.805292-19.10001l-22.499434 19.294439c-34.062793 29.209252-72.504314 52.006468-114.25623 67.759243-43.206034 16.301268-88.775906 24.566512-135.441739 24.566512-102.521978 0-198.907189-39.924291-271.40127-112.417349s-112.417349-168.879292-112.417349-271.40127 39.924291-198.907189 112.417349-271.40127S409.184333 127.926578 511.706311 127.926578c102.521978 0 198.907189 39.924291 271.40127 112.417349s112.417349 168.879292 112.417349 271.40127c0 46.666856-8.265244 92.236727-24.566512 135.441738-15.752776 41.75294-38.549991 80.193437-67.759243 114.256231l-19.294438 22.500456z"
            fill="#bfbfbf"
            p-id="5519"
          ></path>
        </svg>
        <input type="text" placeholder="搜索" @focus="showSearch()" />
      </div>
      <div class="openBtn">打开app</div>
    </div>
    <nav>
      <router-link to="/top/">
        {{ List[0]?.clusterName }}
      </router-link>
      <router-link to="/top/NewRanking">
        {{ List[1]?.clusterName }}
      </router-link>
      <router-link to="/top/HotSale">
        {{ List[2]?.clusterName }}
      </router-link>
      <router-link to="/top/publicPraise">
        {{ List[3]?.clusterName }}
      </router-link>
      <router-link to="/top/AnchorInFluence">
        {{ List[4]?.clusterName }}
      </router-link>
      <router-link to="/top/ChineseCulture">
        {{ List[5]?.clusterName }}
      </router-link>
    </nav>
    <transition name="showPage">
      <searchPage
        class="searchPotion"
        v-show="showSearchPage"
        @Close-Page="closeSearch()"
        :showSearchPage="showSearchPage"
      />
    </transition>
    <!-- 页面内容 -->
    <router-view :List="List" />
  </div>
</template>

<script>
import searchPage from "@/components/searchPage.vue";
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
// import { mapMutations, mapState } from "vuex";
export default {
  name: "RankingList",
  data: function () {
    return {
      List: [],
      showSearchPage: false,
    };
  },
  components: {
    searchPage,
  },
  computed: {},
  methods: {
    getList() {
      this.axios
        .get("https://apis.netstart.cn/ximalaya/rank/queryRankClusterDetail")
        .then((res) => {
          this.List = res.data.data;
        });
    },
    showSearch() {
      this.showSearchPage = true;
      document.querySelector("body").style.overflow = "hidden";
    },
    closeSearch() {
      this.showSearchPage = false;
      document.querySelector("body").style.overflow = "initial";
    },
  },
  created: function () {
    this.getList();
  },
};
</script>

<style lang="less" scoped>
.showPage-enter-active,
.showPage-leave-active {
  transition: transform 0.5s;
}
.showPage-enter,
.showPage-leave-to {
  transform: translateY(100%);
}
.header {
  display: flex;
  align-items: center;
  margin: 10rem;
  height: 30rem;
  .logo {
    flex-shrink: 0;
    width: 60rem;
    height: 100%;
    background-image: url(../../assets/3.png);
    background-position: -25rem 2rem;
    background-size: 250rem;
    // margin-right: 10rem;
  }
  ::placeholder {
    color: #989898;
  }
  .search {
    width: 100%;
    position: relative;
    background-color: #eff1f4;
    border-radius: 50rem;
    margin: 0 10rem;
    svg {
      position: absolute;
      top: 50%;
      left: 8rem;
      transform: translateY(-50%);
      width: 15rem;
      height: 15rem;
    }
    input {
      border: 0;
      outline: 0;
      padding: 8rem 0 8rem 30rem;
      background-color: transparent;
    }
  }
  .openBtn {
    flex-shrink: 0;
    line-height: 10rem;
    padding: 10rem;
    height: 100%;
    border: 1rem solid #f66847;
    color: #f66847;
    border-radius: 50rem;
  }
}
::-webkit-scrollbar {
  display: none;
}
nav {
  width: 375rem;
  height: 45rem;
  position: fixed;
  top: 50rem;
  left: 0rem;
  white-space: nowrap;
  overflow: auto;
  border-bottom: 1rem solid #72727b17;
  background-color: white;
  z-index: 20;
  a {
    display: inline-block;
    height: 26.5rem;
    line-height: 26.5rem;
    padding: 7rem 12.5rem;
    font-size: 14rem;
    color: #72727b;
    vertical-align: top;
    text-decoration: none;
    &:nth-child(1) {
      margin-left: 2.5rem;
    }
    &.router-link-exact-active {
      font-size: 18rem;
      position: relative;
      color: #40404c;
      &::before {
        display: block;
        content: "";
        position: absolute;
        bottom: -17.5rem;
        left: 50%;
        margin-left: -10rem;
        width: 20rem;
        height: 3rem;
        background: red;
      }
    }
  }
}
</style>
