<template>
  <div>
    <div
      class="img"
      :class="{ up: tabhide }"
      v-if="soundSrc.trackDetailInfo?.trackInfo.playPath"
    >
      <div class="mark">
        <!-- 未播放 -->
        <svg
          @click.stop="$emit('play-Music')"
          v-show="!play"
          t="1665907534706"
          class="icon"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="3423"
          width="48"
          height="48"
        >
          <path
            d="M744.727273 551.563636L325.818182 795.927273c-30.254545 18.618182-69.818182-4.654545-69.818182-39.563637v-488.727272c0-34.909091 39.563636-58.181818 69.818182-39.563637l418.909091 244.363637c30.254545 16.290909 30.254545 62.836364 0 79.127272z"
            p-id="3424"
            fill="#f66847"
          ></path>
        </svg>
        <!-- 在播放 -->
        <svg
          @click.stop="$emit('stop-Music')"
          v-show="play"
          t="1665910192838"
          class="icon"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="5369"
          width="48"
          height="48"
        >
          <path
            d="M680 160h128c30.928 0 56 25.072 56 56v593c0 30.928-25.072 56-56 56H680c-30.928 0-56-25.072-56-56V216c0-30.928 25.072-56 56-56z m-464 0h128c30.928 0 56 25.072 56 56v593c0 30.928-25.072 56-56 56H216c-30.928 0-56-25.072-56-56V216c0-30.928 25.072-56 56-56z"
            p-id="5370"
            fill="#f66847"
          ></path>
        </svg>
      </div>
      <canvas ref="canvas" width="300" height="300"></canvas>
      <img
        ref="moveImg"
        :class="{ off: !play }"
        :src="chuli(soundSrc.trackDetailInfo.trackInfo.cover)"
        alt=""
      />
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  props: {
    tabhide: Boolean,
  },
  computed: {
    ...mapState(["soundSrc", "play", "audioplay", "durationchange", "timeupdate"]),
    persontage: function () {
      return this.timeupdate / this.durationchange;
    },
  },
  methods: {
    ...mapMutations(["changeMusic"]),
    chuli: function (res) {
      if (res.startsWith("http")) {
        return res;
      } else if (!res.startsWith("http")) {
        return `https://imagev2.xmcdn.com/${res}!op_type=3&columns=144&rows=144&magick=webp`;
      } else {
        return "1.png";
      }
    },
  },
  watch: {
    persontage: function (n) {
      var canvas = this.$refs.canvas;
      var context = canvas.getContext("2d");

      context.strokeStyle = "#f66847";
      context.clearRect(0, 0, 500, 500);
      context.lineCap = "round";
      context.beginPath();
      context.lineWidth = 15;
      context.arc(150, 150, 120, -0.5 * Math.PI, Math.PI * (2 * n - 0.5));
      // context.closePath();

      context.stroke();
    },
  },
};
</script>

<style lang="less" scoped>
.img {
  width: 57rem;
  height: 58rem;
  background-color: #323232aa;
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 1000;
  transform: translateY(90%);
  transition: all 0.3s;
  &.up {
    transform: translateY(0%);
  }
  .mark {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 12;
    svg {
      width: 25rem;
      height: 25rem;
    }
  }
  @keyframes rotateMove {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  img {
    outline: 3rem solid #f6674761;
    width: 40rem;
    height: 40rem;
    border-radius: 50%;
    animation: rotateMove 3s linear infinite;
    animation-play-state: runninwg;
    &.off {
      animation-play-state: paused;
    }
  }
  canvas {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 55rem;
    height: 55rem;
  }
}
</style>
