<template>
  <ul class="card category" @scroll="srcollEvent">
    <li v-for="item in list" :key="item?.category.categoryId">
      <div
        class="title"
        :class="{ active: currentId === item?.category.categoryId }"
        @click="toClassify(item.category.code, 'all')"
      >
        <p>
          <span class="displayName">
            {{ item?.category.displayName }}
          </span>
          <span v-if="item?.albumCount"> /共{{ item?.albumCount }}张专辑</span>
          <i class="iconfont icon-jiantouyou"></i>
        </p>
      </div>
      <ul class="main">
        <li
          v-for="sub in item?.category.subCategories"
          :key="sub.subCategoryId"
          @click="toClassify(item.category.code, sub.code)"
        >
          {{ sub.metaDataValue }}
        </li>
      </ul>
    </li>
  </ul>
</template>

<script>
import { mapMutations, mapState } from "vuex";
export default {
  props: {
    list: Array,
    keyId: [Number, String],
    cIndex: Number,
  },
  data() {
    return {
      El: null,
    };
  },
  computed: {
    ...mapState(["currentId"]),
    ElH() {
      if (this.El) {
        let el = this.El.map((o) => o.offsetHeight);
        return el.map((e, i, arr) => {
          let tmp = 0;
          if (i) {
            tmp = arr.slice(0, i).reduce((o, t) => o + t);
          } else {
            tmp = 0;
          }
          return tmp;
        });
      } else {
        return null;
      }
    },
  },
  methods: {
    ...mapMutations(["setCurrentId"]),
    // 去
    toClassify(one, sub) {
      console.log(one, sub);
      this.$router.push({ path: `/classify/${one}/`, query: { sub } });
    },
    srcollEvent(event) {
      if (this.ElH) {
        let h = event.target.scrollTop;
        let tmpI = this.ElH.slice(0).findIndex((o) => {
          return o >= h;
        });
        if (tmpI !== -1) {
          this.setCurrentId(this.list[tmpI].category.categoryId);
        }
      }
    },
  },
  watch: {
    // list有时获取dom
    list(n) {
      if (n.length) {
        this.$nextTick(() => {
          this.El = [...document.querySelectorAll(".category>li")];
        });
      }
    },
    cIndex(n) {
      console.log(n);
      if (this.ElH) {
        document.querySelector(".category").scrollTo({
          top: this.ElH[n],
          // behavior: "smooth",
        });
      }
    },
  },
};
</script>

<style lang="less" scoped>
ul.card {
  width: 280rem;
  height: 100vh;
  margin-top: 55rem;
  overflow: auto;
  flex-grow: 1;
  background-color: #fff;
  & > li {
    width: 280rem;
    .title {
      line-height: 50rem;
      height: 50rem;
      padding-left: 15rem;
      color: #40404c;
      font-weight: 600;
      font-size: 14rem;
      &.active {
        color: #fe7959;
        p {
          .displayName {
            font-size: 15rem;
            font-weight: bold;
          }
          span {
            color: #fe7959;
          }
        }
      }
      p {
        position: relative;
        &::after {
          content: "";
          display: block;
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 1rem;
          background-color: rgba(204, 204, 204, 0.429);
        }
        .displayName {
          font-size: 15rem;
          font-weight: bold;
        }
        span {
          font-weight: 400;
          font-size: 12rem;
        }
        i {
          position: absolute;
          top: 50%;
          right: 25rem;
          font-size: 12px;
          color: #a3a3ac;
          position: absolute;
          transform: translateY(-50%);
        }
      }
    }
    ul.main {
      padding: 7.5rem 0 7.5rem 7.5rem;
      display: flex;
      flex-wrap: wrap;
      li {
        line-height: 20rem;
        padding: 7.5rem;
        font-size: 14rem;
        color: #7e8c8d;
      }
    }
  }
}
</style>
