<template>
  <div class="sidebar-container">
    <ul class="sidebar">
      <li
        v-for="(item, index) in list"
        :key="item.category.categoryId"
        :class="{ active: keyId === item.category.categoryId }"
        @click.stop="clickHandler(item, index)"
      >
        <!-- @click="setCurrentId(item.category.categoryId)" -->
        {{ item?.category.displayName }}
      </li>
    </ul>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
export default {
  props: {
    list: Array,
    keyId: [Number, String],
  },
  data() {
    return {
      El: null,
      // ElH:[],
    };
  },
  computed: {
    ElH() {
      if (this.El) {
        let el = this.El.map((o) => o.offsetHeight);
        return el.map((e, i, arr) => {
          let tmp = 0;
          if (i) {
            tmp = arr.slice(0, i).reduce((o, t) => o + t);
          } else {
            tmp = 0;
          }
          return tmp;
        });
      } else {
        return null;
      }
    },
  },
  methods: {
    ...mapMutations(["setCurrentId"]),
    clickHandler(item, index) {
      this.setCurrentId(item.category.categoryId);
      this.$emit("toggleCate", index);
    },
  },
  watch: {
    // list有时获取dom
    list(n) {
      if (n.length) {
        this.$nextTick(() => {
          console.log(document.querySelectorAll(".sidebar>li"));
          this.El = [...document.querySelectorAll(".sidebar>li")];
        });
      }
    },
    // 改变keyId滚动
    keyId(n) {
      let tmpIndex = this.list.findIndex((o) => o.category.categoryId === n);
      if (this.ElH) {
        document.querySelector(".sidebar-container").scrollTo({
          top: this.ElH[tmpIndex],
        });
      }
    },
  },
};
</script>

<style lang="less" scoped>
div {
  width: 90rem;
  margin-top: 55rem;
  height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
  ul {
    li {
      width: 90rem;
      min-height: 50rem;
      padding: 15rem 5rem;
      background-color: #fff;
      font-size: 14rem;
      text-align: center;
      color: #72727b;
      position: relative;
      &.active {
        &::before {
          content: "";
          display: block;
          position: absolute;
          left: 0;
          top: 0;
          width: 5rem;
          height: 50rem;
          background-color: #f86442;
        }
      }
      &::after {
        content: "";
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 1rem;
        background-color: rgba(204, 204, 204, 0.433);
      }
    }
  }
}
</style>
