<template>
  <div class="classify-page">
    <div class="loading" v-show="showLoading === true">
      <img src="../assets/loading.gif" alt="" />
      页面加载中...
    </div>

    <div class="headerBox">
      <div class="header">
        <div class="mark"></div>
        <div class="logo" @click="$router.push({ path: '/category' })">
          <svg
            t="1666160732318"
            class="icon"
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            p-id="3114"
            width="48"
            height="48"
          >
            <path
              d="M384 512L731.733333 202.666667c17.066667-14.933333 19.2-42.666667 4.266667-59.733334-14.933333-17.066667-42.666667-19.2-59.733333-4.266666l-384 341.333333c-10.666667 8.533333-14.933333 19.2-14.933334 32s4.266667 23.466667 14.933334 32l384 341.333333c8.533333 6.4 19.2 10.666667 27.733333 10.666667 12.8 0 23.466667-4.266667 32-14.933333 14.933333-17.066667 14.933333-44.8-4.266667-59.733334L384 512z"
              p-id="3115"
              fill="#2c2c2c"
            ></path>
          </svg>
          {{ pageDataAll?.categoryDisplayName }}
        </div>
        <div class="search">
          <svg
            t="1664715880181"
            class="icon"
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            p-id="5518"
            width="48"
            height="48"
          >
            <path
              d="M851.710029 803.043632c67.147306-78.301344 107.720374-180.059935 107.720373-291.298435 0-247.27171-200.453405-447.724091-447.724091-447.724091S63.981197 264.473487 63.981197 511.745197s200.453405 447.724091 447.724091 447.724091c111.2385 0 212.997092-40.573067 291.298435-107.720374l97.632622 97.632621c13.449313 13.449313 35.255968 13.449313 48.706305 0 13.449313-13.449313 13.449313-35.255968 0-48.706305l-97.632621-97.631598z m-67.805292-19.10001l-22.499434 19.294439c-34.062793 29.209252-72.504314 52.006468-114.25623 67.759243-43.206034 16.301268-88.775906 24.566512-135.441739 24.566512-102.521978 0-198.907189-39.924291-271.40127-112.417349s-112.417349-168.879292-112.417349-271.40127 39.924291-198.907189 112.417349-271.40127S409.184333 127.926578 511.706311 127.926578c102.521978 0 198.907189 39.924291 271.40127 112.417349s112.417349 168.879292 112.417349 271.40127c0 46.666856-8.265244 92.236727-24.566512 135.441738-15.752776 41.75294-38.549991 80.193437-67.759243 114.256231l-19.294438 22.500456z"
              fill="#bfbfbf"
              p-id="5519"
            ></path>
          </svg>
          <input type="text" placeholder="搜索" @focus="showSearch()" />
        </div>
        <div class="openBtn">打开app</div>
      </div>
    </div>
    <transition name="showPage">
      <searchPage
        class="searchPotion"
        v-show="showSearchPage"
        @Close-Page="closeSearch()"
        :showSearchPage="showSearchPage"
      />
    </transition>

    <div class="warp">
      <!-- 未打开 -->
      <div class="tabs">
        <ul>
          <li :class="{ active: currentClassify === 'all' }" @click="toSub('all')">
            全部
          </li>
          <li
            v-for="sub in pageDataAll?.subCategories"
            :class="{ active: currentClassify === sub.code }"
            :key="sub.subCategoryId"
            @click="toSub(sub.code)"
          >
            {{ sub.metaDataValue }}
          </li>
        </ul>
      </div>
      <span @click="showOpen = true">
        <svg
          t="1666084994794"
          class="icon"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="4102"
          width="48"
          height="48"
        >
          <path
            d="M904.533333 311.466667c-17.066667-17.066667-42.666667-17.066667-59.733333 0L512 644.266667 179.2 311.466667c-17.066667-17.066667-42.666667-17.066667-59.733333 0-17.066667 17.066667-17.066667 42.666667 0 59.733333l362.666666 362.666667c8.533333 8.533333 19.2 12.8 29.866667 12.8s21.333333-4.266667 29.866667-12.8l362.666666-362.666667c17.066667-17.066667 17.066667-42.666667 0-59.733333z"
            p-id="4103"
            fill="#f66847"
          ></path>
        </svg>
      </span>

      <div class="open-up" :class="{ open: showOpen }">
        <!-- 已打开 -->
        <div class="open-title">
          <div>请选择分类</div>
          <span class="icon" @click="showOpen = false">
            <svg
              t="1666084528705"
              class="icon"
              viewBox="0 0 1024 1024"
              version="1.1"
              xmlns="http://www.w3.org/2000/svg"
              p-id="3139"
              width="48"
              height="48"
            >
              <path
                d="M793.024 710.272a32 32 0 1 0 45.952-44.544l-310.304-320a32 32 0 0 0-46.4 0.48l-297.696 320a32 32 0 0 0 46.848 43.584l274.752-295.328 286.848 295.808z"
                p-id="3140"
                fill="#f66847"
              ></path>
            </svg>
          </span>
        </div>

        <ul class="open-container">
          <li @click="toSub('all')">全部</li>
          <li
            v-for="sub in pageDataAll?.subCategories"
            :key="sub.subCategoryId"
            @click="toSub(sub.code)"
          >
            {{ sub.metaDataValue }}
          </li>
        </ul>
      </div>
    </div>
    <div class="san-classify">
      <div class="menu">
        <span
          v-for="m in menu"
          :key="m.code"
          :class="{ active: currentSan === m.code }"
          @click="currentSan = m.code"
          >{{ m.text }}</span
        >
      </div>
    </div>
    <ClassifyPageData :info="pageData" />
  </div>
</template>

<script>
import { mapMutations } from "vuex";
import { getClassifyMain } from "@/apis";
import ClassifyPageData from "@/components/category/ClassifyPageData.vue";
import SearchPage from "@/components/searchPage.vue";
export default {
  name: "ClassifyView",
  components: {
    ClassifyPageData,
    SearchPage,
  },
  data() {
    return {
      // 数据
      pageDataAll: null,
      pageData: null,
      showSearchPage: false,
      showLoading: true,
      menu: [
        {
          text: "综合排序",
          code: 0,
        },
        {
          text: "最多播放",
          code: 2,
        },
        {
          text: "最近更新",
          code: 1,
        },
      ],
      // 当前二级分类是
      currentClassify: "",
      // 当前三级筛选
      currentSan: 0,
      showOpen: false,
    };
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      vm.currentClassify = to.query.sub;
    });
  },
  beforeRouteLeave(to, from, next) {
    this.showOpen = false;
    // document.body
    next();
  },
  created() {
    this.showLoading = true;
    getClassifyMain({
      categoryCode: this.$route.params.link,
      subCategoryCode: this.currentClassify,
    }).then((res) => {
      this.showLoading = false;
      this.pageDataAll = res.data.data;
      this.pageData = res.data.data.firstPageCategoryAlbums.albumBriefDetailInfos;
    });
  },
  methods: {
    ...mapMutations(["toggleShowSearch"]),
    toSub(code) {
      // console.log(code);
      this.showOpen = false;
      this.currentClassify = code;
    },
    // 请求方法
    getPageData(sub) {
      this.showLoading = true;
      getClassifyMain({
        categoryCode: this.$route.params.link,
        subCategoryCode: sub,
      }).then((res) => {
        this.showLoading = false;
        this.pageData = res.data.data.firstPageCategoryAlbums.albumBriefDetailInfos;
      });
    },
    showSearch() {
      this.showSearchPage = true;
      document.querySelector("body").style.overflow = "hidden";
    },
    closeSearch() {
      this.showSearchPage = false;
      document.querySelector("body").style.overflow = "initial";
    },
  },
  watch: {
    currentClassify(n) {
      // console.log(n);
      if (n === "all") {
        this.getPageData("");
      } else {
        this.getPageData(this.currentClassify);
      }
      // console.log('执行');
      // this.$router.push({path:`/${this.$route.params.link}/${n}`})
    },
    currentSan(n) {
      this.showLoading = true;

      getClassifyMain({
        categoryCode: this.$route.params.link,
        subCategoryCode: this.currentClassify === "all" ? "" : this.currentClassify,
        sort: n,
      }).then((res) => {
        this.showLoading = false;
        this.pageData = res.data.data.firstPageCategoryAlbums.albumBriefDetailInfos;
      });
    },

    showOpen(n) {
      if (n) {
        document.body.style.overflow = "hidden";
      } else {
        document.body.style.overflow = "";
      }
    },
  },
};
</script>

<style lang="less" scoped>
.loading {
  display: flex;
  justify-content: start;
  align-items: center;
  flex-direction: column;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  z-index: 100;
  font-size: 15rem;
  img {
    margin-top: 100rem;
  }
}
.showPage-enter-active,
.showPage-leave-active {
  transition: transform 0.5s;
}
.showPage-enter,
.showPage-leave-to {
  transform: translateY(100%);
}

.headerBox {
  width: 100%;
  height: 50rem;
  background-color: #fff;
  position: fixed;
  top: 0;
  z-index: 10;
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 10;
  display: flex;
  align-items: center;
  margin: 10rem 0;
  padding: 0 10rem;
  height: 30rem;
  // .headerBox {
  //   height: 50rem;
  //   background: #fff;
  // }
  .logo {
    flex-shrink: 0;
    // width: 60rem;
    // height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18rem;
    svg {
      width: 20rem;
      height: 20rem;
    }
    // margin-right: 10rem;
  }
  ::placeholder {
    color: #989898;
  }
  .search {
    width: 100%;
    position: relative;
    background-color: #eff1f4;
    border-radius: 50rem;
    margin: 0 10rem;
    svg {
      position: absolute;
      top: 50%;
      left: 8rem;
      transform: translateY(-50%);
      width: 15rem;
      height: 15rem;
    }
    input {
      border: 0;
      outline: 0;
      padding: 8rem 0 8rem 30rem;
      background-color: transparent;
    }
  }
  .openBtn {
    flex-shrink: 0;
    line-height: 10rem;
    padding: 10rem;
    height: 100%;
    border: 1rem solid #f66847;
    color: #f66847;
    border-radius: 50rem;
  }
}
.classify-page {
  header {
    div.head-t {
      padding: 15rem 15rem 2rem;
      display: flex;
      align-items: center;
      .icon {
        white-space: nowrap;
        font-size: 18rem;
        color: #333;
        margin-left: -10rem;
      }
      a.head-input {
        background-color: #eff1f4;
        width: 182rem;
        height: 30rem;
        border-radius: 30rem;
        margin-left: 10rem;
        flex-grow: 1;
        form {
          height: 100%;
          display: flex;
          align-items: center;
          i {
            width: 14rem;
            height: 14rem;
            margin: 0 8rem;
            font-size: 14rem;
            color: #ababab;
          }
          p {
            font-size: 12rem;
            color: #ababab;
          }
        }
      }
      a.openApp {
        width: 68rem;
        height: 30rem;
        border-radius: 30rem;
        border: 1rem solid #f66847;
        color: #f86442;
        font-size: 12rem;
        line-height: 30rem;
        text-align: center;
        text-decoration: none;
        margin-left: 10rem;
      }
    }
  }
  .warp {
    display: flex;
    position: fixed;
    width: 100%;
    background: #fff;
    z-index: 9;
    align-items: center;
    border: 1rem solid #ccc;
    .tabs {
      margin-top: 50rem;
      height: 45rem;
      // padding-left: 5rem;
      overflow: auto;
      position: relative;
      &::-webkit-scrollbar {
        display: none;
      }
      ul {
        display: flex;
        flex-wrap: nowrap;
        li {
          position: relative;
          &.active {
            color: #40404c;
            font-size: 18rem;
            &::after {
              content: "";
              width: 33rem;
              height: 3rem;
              background-color: #f06742;
              display: block;
              position: absolute;
              bottom: 0;
              left: 50%;
              transform: translateX(-50%);
            }
          }
          flex-shrink: 0;
          padding: 0 15rem;
          font-size: 14rem;
          height: 45rem;
          line-height: 45rem;
          color: #a3a3ac;
          // flex-wrap: nowrap;
        }
      }
    }
    > span {
      width: 44rem;
      height: 44rem;
      line-height: 44rem;
      text-align: center;
      margin-left: 10rem;
      margin-top: 50rem;
      border-left: 0.5rem solid #ccc;
      background-color: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;
      svg {
        width: 15rem;
        height: 15rem;
      }
    }
    .open-up {
      position: absolute;
      top: 50rem;
      left: 0;
      width: 100%;
      background-color: #fff;
      z-index: 999;
      transition: all 0.5s;
      opacity: 0;
      visibility: hidden;
      &.open {
        visibility: initial;

        opacity: 1;
        .open-title {
          opacity: 1;
        }
        ul.open-container {
          opacity: 1;
          transform: translateY(0%);
        }
      }
      .open-title {
        opacity: 0;
        width: 100%;
        display: flex;
        justify-content: space-between;
        height: 45rem;
        border-bottom: 1rem solid #e8e8e8;
        position: relative;
        padding-left: 15rem;

        div {
          color: #a3a3ac;
          font-size: 14rem;
          line-height: 45rem;
        }
        > span {
          svg {
            width: 18rem;
            height: 18rem;
          }
          width: 44rem;
          display: flex;
          align-items: center;
          justify-content: center;
          border-left: 1rem solid #ccc;
          color: #f06742;
        }
      }
      ul.open-container {
        transform: translateY(-150%);
        padding: 7.5rem;
        transition: all 0.3s;
        display: flex;
        flex-wrap: wrap;
        box-shadow: 0 -1rem 1rem 0 rgba(100, 100, 100, 0.2);
        height: 345rem;
        overflow: auto;
        li {
          width: 27%;
          height: 32rem;
          font-size: 16rem;
          margin: 7.5rem;
          text-align: center;
          line-height: 32rem;
          flex-grow: 1;
          color: #40404c;
          background-color: #f3f4f5;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
    }
  }
  .san-classify {
    position: relative;
    top: 100rem;
    background: #fff;
    z-index: 7;
    .menu {
      display: flex;
      padding-left: 15rem;
      span {
        width: 82rem;
        height: 28rem;
        &.active {
          color: #f86442;
          border-radius: 15rem;
          border: 1rem solid rgba(254, 121, 89, 0.5);
          margin-right: 4rem;
        }
        color: #a3a3ac;
        display: inline-block;
        border: 1rem solid transparent;
        text-align: center;
        font-size: 14rem;
        margin: 8rem 0;
        line-height: 28rem;
      }
    }
  }
}
</style>
